            </select>
        </div>
        <div>
            <label>Department:</label>
            <select name="department" v-model="department">
                <option value="all">All</option>
                <option value="CE">CE</option>
                <option value="CP">CP</option>
                <option value="EL">EL</option>
                <option value="EE">EE</option>
                <option value="ME">ME</option>
                <option value="PE">PE</option>
                <option value="EC">EC</option>
                <option value="IT">IT</option>
            </select>
        </div>
        <button @click="filter" class="mr-4 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded ml-2">
                filter
        </button>
        <button @click="printData" class="mr-2 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">print</button>
        <button @click="download" class="mr-2 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">Download</button>
       
        <p>No of participants: {{filtered.length}}</p>
        <table id="printTable" style="border: 1px solid black; border-collapse: collapse"> 
            <tr style="border: 1px solid black">
                <th style="border: 1px solid black; padding: 5px;">ID</th>
                <th style="border: 1px solid black; padding: 5px;">Name</th>
                <th style="border: 1px solid black; padding: 5px;">Mobile</th>
                <th style="border: 1px solid black; padding: 5px;">Email</th>
                <th style="border: 1px solid black; padding: 5px;">Code</th>
                <th style="border: 1px solid black; padding: 5px;">Gender</th>
                <th style="border: 1px solid black; padding: 5px;">Block</th>
                <th style="border: 1px solid black; padding: 5px;">Department</th>
                <th style="border: 1px solid black; padding: 5px;">Date</th>
            </tr>
            <tr v-for="(participant, index) in filtered" :key="index" style="border: 1px solid black">
                <td style="border: 1px solid black; padding: 5px;">{{participant.student_id}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.name}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.mobile}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.email}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.code}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.gender}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.block}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.department}}</td>
                <td style="border: 1px solid black; padding: 5px;">{{participant.date}}</td>
            </tr>
        </table>
    </div>
</template>
<script>
import firebaseInit from '../../firebaseInit'
import * as firebase from 'firebase/app';
import 'firebase/auth';
export default {
    name: 'participants',
    data(){
        return{
            participants: [],
            filtered: [],
            event: "all",
            gender: "all",
            block: "all",
            date: "all",
            department: "all"
        }
    },
    methods: {
    async signOut(){
        try{
            await firebase.auth().signOut();
            this.$router.replace({name: "login"});
        }catch(err){
            alert("error: "+err);
        }
    },
        //start
    convertToCSV(objArray) 
    {
    var array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
    var str = '';

    for (var i = 0; i < array.length; i++) {
        var line = '';
        for (var index in array[i]) {
            if (line != '') line += ','

            line += array[i][index];
        }

        str += line + '\r\n';
    }

    return str;
},
exportCSVFile(headers, items, fileTitle)
    {
    if (headers) {
        items.unshift(headers);
    }

    var jsonObject = JSON.stringify(items);

    var csv = this.convertToCSV(jsonObject);

    var exportedFilenmae = fileTitle + '.csv' || 'export.csv';

    var blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    if (navigator.msSaveBlob) { // IE 10+
        navigator.msSaveBlob(blob, exportedFilenmae);
    } else {
        var link = document.createElement("a");
        if (link.download !== undefined) { // feature detection
            // Browsers that support HTML5 download attribute
            var url = URL.createObjectURL(blob);
            link.setAttribute("href", url);
            link.setAttribute("download", exportedFilenmae);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
},

    

 download(){
  var headers = {
      student_id:"Student_id",
      name: 'Name'.replace(/,/g, ''),
      mobile:"Mobile",
      email:"Email",
      gender:"Gender",
      block:"Block",
      department:"Department",
      date:"Date"
     // event:"event"
  };
 
  var itemsFormatted = [];

  
 
 var i=0;
    for(i=0;i<this.filtered.length;i++)
    {
      itemsFormatted.push({
          student_id:this.filtered[i].student_id,
          name: this.filtered[i].name.replace(/,/g, ''),
          mobile:this.filtered[i].mobile,
          email:this.filtered[i].email,
          //code:this.filtered[i].code,
          gender:this.filtered[i].gender,
          block:this.filtered[i].block,
          department:this.filtered[i].department,
          date:this.filtered[i].date
         // events:this.filtered[i].events
          //year: item.year,
          //e1: item.e1,
          //e2: item.e2
      });
  }

  var fileTitle = 'registration'; 

  this.exportCSVFile(headers, itemsFormatted, fileTitle);
},


        //end
        printData(){
            var divToPrint=document.getElementById("printTable");
            let newWin= window.open("");
            newWin.document.write(divToPrint.outerHTML);
            newWin.print();
            newWin.close();
        },
        filter(){
            this.filtered = [];
            if(this.event == "all"){
                this.filtered = this.participants;
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "combo"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1){
                        this.filtered.push(participant);
                    }
                });
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "e1"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1 || events.substring(5,6)== 1){
                        this.filtered.push(participant);
                    }
                });
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "e2"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1 || events.substring(4,5)== 1){
                        this.filtered.push(participant);
                    }
                });
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "e3"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1 || events.substring(3,4)== 1){
                        this.filtered.push(participant);
                    }
                });
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "e4"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1 || events.substring(2,3)== 1){
                        this.filtered.push(participant);
                    }
                })
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                return;
            }
            if(this.event == "e5"){
                this.participants.forEach((participant)=>{
                    let events = participant.events;
                    if(events.substring(0,1) == 1 || events.substring(1,2)== 1){
                        this.filtered.push(participant);
                    }
                });
                this.filterGender();
                this.filterBlock();
                this.filterDate();
                this.filterDepartment();
                //this.filtered.sort(this.compare);
                return;
            }
        },
        filterDepartment(){
            let temp = this.filtered;
            this.filtered = [];
            if(this.department == "all"){
                this.filtered = temp;
                return;
            }
            if(this.department == "CE"){
                temp.forEach((participant)=>{
                    if(participant.department == "CE"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "CP"){
                temp.forEach((participant)=>{
                    if(participant.department == "CP"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "EL"){
                temp.forEach((participant)=>{
                    if(participant.department == "EL"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "EE"){
                temp.forEach((participant)=>{
                    if(participant.department == "EE"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "ME"){
                temp.forEach((participant)=>{
                    if(participant.department == "ME"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "PE"){
                temp.forEach((participant)=>{
                    if(participant.department == "PE"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "EC"){
                temp.forEach((participant)=>{
                    if(participant.department == "EC"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.department == "IT"){
                temp.forEach((participant)=>{
                    if(participant.department == "IT"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
        },
        filterDate(){
            let temp = this.filtered;
            this.filtered = [];
            if(this.date == "all"){
                this.filtered = temp;
                return;
            }
            if(this.date == "2020/02/18"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/18"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/19"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/19"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/20"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/20"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/21"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/21"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/22"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/22"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/23"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/23"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/24"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/24"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/25"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/25"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "2020/02/12"){
                temp.forEach((participant)=>{
                    if(participant.date == "2020/02/12"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.date == "other"){
                temp.forEach((participant)=>{
                    if(participant.date != "2020/02/18" &&participant.date != "2020/02/19"&&participant.date != "2020/02/20"&&participant.date != "2020/02/21"&&participant.date != "2020/02/22"&&participant.date != "2020/02/23"&&participant.date != "2020/02/24"&&participant.date != "2020/02/25"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
        },
        filterBlock(){
            let temp = this.filtered;
            this.filtered = [];
            if(this.block == "all"){
                this.filtered = temp;
                return;
            }
            if(this.block == "A"){
                temp.forEach((participant)=>{
                    if(participant.block == "A"){
                        this.filtered.push(participant);
                    }
                });
