                return;
            }
            if(this.gender == "M"){
                temp.forEach((participant)=>{
                    if(participant.gender == "M"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
            if(this.gender == "F"){
                temp.forEach((participant)=>{
                    if(participant.gender == "F"){
                        this.filtered.push(participant);
                    }
                });
                return;
            }
        },
        compare(a, b){
            if ( a.student_id < b.student_id ){
                return -1;
            }
            if ( a.student_id> b.student_id ){
                return 1;
            }
            return 0;
        }
    },
    created(){
        let parts = [];
            //let code = null;
            firebaseInit.collection("participants").get().then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    // doc.data() is never undefined for query doc snapshots
                    //code = doc.data().code;
                    let neww={
                        student_id: doc.data()["student_id"],
                        name: doc.data()["name"],
                        code: doc.data()["code"],
                        mobile: doc.data()["mobile"],
                        email: doc.data()["email"],
                        gender: doc.data()["code"].substring(8,9),
                        block: doc.data()["code"].substring(0,1),
                        department: doc.data()["code"].substring(3,5),
                        events: parseInt(doc.data()["code"].substring(9,11)).toString(2).padStart(6, '0'),
                        date: doc.data()["currentDateWithFormat"],
                    };
                    //for(let i =0; i<100;i++){
                        parts.push(neww);
                    //}
                });                
            });
            this.participants = parts;
    }
}
</script>
<style scoped>
    .participants{
        min-height: calc(100vh - 9rem);
    }
    table { page-break-inside:auto }
    tr    { page-break-inside:avoid; page-break-after:auto }
