import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import Landing from './components/Landing'
import Events from './components/Events'
import Results from  './components/Results'
import About from './components/About'
import EventOne from './components/Events/EventOne'
import EventTwo from './components/Events/EventTwo'
import EventThree from './components/Events/EventThree'
import EventFour from './components/Events/EventFour'
import EventFive from './components/Events/EventFive'
import addParticipant from './components/Admin/addParticipant';
import participants from './components/Admin/participants'
import addImage from './components/Admin/addImage'
import login from './components/Admin/login'

import * as firebase from 'firebase/app';
import 'firebase/auth';
import VueLazyLoad from 'vue-lazyload'
//Vue.config.productionTip = false

Vue.use(VueRouter);
Vue.use(VueLazyLoad);

const routes =[
  {path: '/', component: Landing},
  {path: '/events',component: Events,
    children: [
      {path: '',component: EventOne},
      {path: '1',component: EventOne},
      {path: '2',component: EventTwo},
      {path: '3',component: EventThree},
      {path: '4',component: EventFour},
      {path: '5',component: EventFive},
    ]
  },
  {path: '/results',component: Results},
  {path: '/about',component: About},
  {path: '/addparticipant',name: 'add',component: addParticipant, meta: {requiresAuth: true}},
  {path: '/participants',component: participants, name: 'participants', meta: {requiresAuth: true}},
  {path: '/login', name: 'login', component: login},
  {path: '/addimage',name: 'addimg',component: addImage, meta: {requiresAuth: true}},

    
];

const router = new VueRouter({
  routes,
  
})
/*
new Vue({
  render: h => h(App),
  router,
}).$mount('#app')*/

router.beforeEach((to,from,next)=>{
  const requiresAuth = to.matched.some(record=>record.meta.requiresAuth);
  const isAuthenticated = firebase.auth().currentUser;
  if(requiresAuth && !isAuthenticated){
    next("/login");
  }else{
    next();
  }
})
//Vue.config.productionTip = false;
let app;
firebase.auth().onAuthStateChanged(()=>{
  if(!app){
    app = 
    new Vue({
      render: h => h(App),
      router,
    }).$mount('#app');
  }
})

