<template>
    <div>
      <h1 class="heading">Results</h1>
      <div id="app" :style="require('vue-image-lightbox/dist/vue-image-lightbox.min.css')" class="row container mx-auto h-ful gallery">
        <div>
          <ul>
            <li 
              v-for="(image, index) in images" 
              style="display: inline-block" :key="index"
            >
              <img 
                v-lazy="image.src" 
                style="height: 200px" 
                @click="openGallery(index)"
                class="m-2"
              >
            </li>
          </ul>
          <LightBox 
            :images="images" 
            ref="lightbox"
            :show-caption="true"
            :show-light-box="false"
          >
          </LightBox>
        </div>
      </div>
    </div>
</template>
<script>
import LightBox from 'vue-image-lightbox'
import firebaseInit from '../firebaseInit'

let images = [];
firebaseInit.collection("images").get().then(function(querySnapshot) {
          querySnapshot.forEach(function(doc) {
              // doc.data() is never undefined for query doc snapshots
              //console.log(doc.id, " => ", doc.data());
              images.push(doc.data());
            });
      });
export default {
    name: 'results',
    components: {
        LightBox,
    },
    methods: {
        openGallery(index) {
        this.$refs.lightbox.showImage(index)
        }
    },
    data(){
        return{
            images,
        }
    },
}
</script>
<style scoped>
  .gallery{
    min-height: calc(100vh - 9rem);
  }
  .heading{
    font-size: 4vw;
    text-decoration: underline;
    text-align: center;
    margin-bottom: 20px;
  }
  @media(max-width: 768px){
    .heading{
      font-size: 8vw;
    }
  }
</style>