<template>
    <div class="container mx-auto ">
          <div class="flex items-center justify-between my-2">
                <button   @click="signOut()" class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
                    logout
                </button>
        </div>
          <div class="bg-gray-300 shadow-md rounded px-8 pt-6 pb-8">
              <h1 class="text-center  mb-4 text-lg font-bold"> Add a participant</h1>
              <div class="mb-2 relative flex-grow mr-2">
                    <label class="block text-gray-700 text-sm font-bold mb-2" for="block">
                        Block
                    </label>
                    <select id="block" v-model="block" :disabled="true"  class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                        <option value="A">A</option>
                        <option value="D">D</option>
                        <option value="F">F</option>
                        <option value="E">E</option>
                    </select>
                    <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700 mt-6">
                        <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                    </div>      
                </div>
                <div class="flex">
                    <div class="mb-2 flex-grow mr-2">
                        <label class="block text-gray-700 text-sm font-bold mb-2" for="student_id">
                            Student Id
                        </label>
                        <input  v-model.trim="id" @blur="fetchData" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="id" type="text" placeholder="Student Id">
                    </div>
                    <div class="mb-2 flex-grow">
                        <label class="block text-gray-700 text-sm font-bold mb-2" for="name">
                            Name
                        </label>
                        <input  v-model="name" v-if="!updateReq" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="student_name" type="text" placeholder="Name">
                        <input   v-bind:value="this.fetchedData.name" v-else :disabled="updateReq" class="shadow  bg-gray-500 appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="student_name" type="text" placeholder="Name">
                    </div>
                </div>
                <div class="flex">
                    <div class="mb-2 flex-grow mr-2">
                        <label class="block text-gray-700 text-sm font-bold mb-2" for="mobile">
                            Mobile
                        </label>
                        <input  v-model="mobile" v-if="!updateReq" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="mobile" type="text" placeholder="Mobile">
                        <input v-bind:value="this.fetchedData.mobile" v-else :disabled="updateReq" class="shadow bg-gray-500 appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="mobile" type="text" placeholder="Mobile">
                    </div>
                    <div class="mb-2 relative flex-grow w-64">
                        <label for="gender" class="block text-gray-700 text-sm font-bold mb-2">
                            Gender
                        </label>
                        <select  v-model="gender" v-if="!updateReq"  name="gender" id="gender" class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                            <option value="M">Male</option>
                            <option value="F">Female</option>
                        </select>
                        <select :value="this.parsedData.gender" v-else :disabled="updateReq"  name="gender" id="gender" class="block appearance-none w-full bg-gray-500 border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                            <option value="M" >Male</option>
                            <option value="F">Female</option>
                        </select>
                        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700 mt-6">
                            <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                        </div>                    
                    </div>
            </div>

            <div class="mb-2">
                <label class="block text-gray-700 text-sm font-bold mb-2" for="emai;">
                    Email
                </label>
                <input  v-model="email" v-if="!updateReq" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="email" type="email" placeholder="Email">
                <input  v-bind:value="this.fetchedData.email" v-else :disabled="updateReq" class="shadow bg-gray-500 appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="email" type="email" placeholder="Email">
            </div>
            <div class="p-2">
                <label for="combo" class="mr-2">Combo</label>
                <input  v-model="combo" @change="comboPressed" :disabled="comboDisable || updateReq" type="checkbox" value="combo">
            </div>
            <div  class="flex flex-wrap mb-4">
                    <div class="p-2">
                        <label for="e1" class="mr-2">Event 1</label>
                        <input  v-model="e1" @change="otherPressed" :disabled="combo || (updateReq && parsedData.e1)" type="checkbox" value="e1">
                    </div>
                    <div class="p-2">
                        <label for="e2" class="mr-2">Event 2</label>
                        <input v-model="e2" @change="otherPressed" :disabled="combo || (updateReq && parsedData.e2)" type="checkbox" value="e2">
                    </div>
                    <div class="p-2">
                        <label for="e3" class="mr-2">Event 3</label>
                        <input v-model="e3" @change="otherPressed" :disabled="combo || (updateReq && parsedData.e3)" type="checkbox" value="e3">
                    </div>
                    <div class="p-2">
                        <label for="e4" class="mr-2">Event 4</label>
                        <input v-model="e4" @change="otherPressed" :disabled="combo || (updateReq && parsedData.e4)" type="checkbox" value="e4">
                    </div>
                    <div class="p-2">
                        <label for="e5" class="mr-2">Event 5</label>
                        <input v-model="e5" @change="otherPressed" :disabled="combo || (updateReq && parsedData.e5)" type="checkbox" value="e5">
                    </div>
                </div>

            <div class="flex items-center justify-between">
                <button   @click="submit()" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
                    Submit
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import firebaseInit from '../../firebaseInit'
import * as firebase from 'firebase/app';
import 'firebase/auth';

export default {
    name: 'add-participant',
data(){
        return{
            block: 'A',
            id : '',
            name : '',
            mobile : '',
            gender: '',
            email : '',
            code: '',
            combo: false,
            e1: false,
            e2: false,
            e3: false,
            e4: false,
            e5: false,
            comboDisable: false,
            events: 0,
            updateReq: false,
            parsedData: {
                block: '',
                id: '',
                gender: '',
                combo: false,
                events: 0,
                e1: false,
                e2: false,
                e3: false,
                e4: false,
                e5: false
            },
            fetchedData: {
                student_id: '',
                mobile: '',
                email: '',
                code: '',
                name: '',
            },
        }

    },
    created(){
        let user = firebase.auth().currentUser;
        let email = user.email;
        let block = email.substring(0,1);
        this.block = block.toUpperCase();
    },
    methods:{
        async signOut(){
            try{
                await firebase.auth().signOut();
                this.$router.replace({name: "login"});
            }catch(err){
                alert("err: "+err);
            }
        },
        comboPressed(){
            if(this.combo == true){
                this.e1 = true;
                this.e2 = true;
                this.e3 = true;
                this.e4 = true;
                this.e5 = true;
            }else{
                this.e1 = false;
                this.e2 = false;
                this.e3 = false;
                this.e4 = false;
                this.e5 = false;
            }
        },
        clearData(){
            this.clearFetchedData();
            this.clearParsedData();
            this.clearEvents();
            this.name = "";
            this.email = '';
            this.mobile = '';
            this.gender = '';
            this.code = '';
        },
        clearFetchedData(){
            this.fetchedData.student_id = '';
            this.fetchedData.mobile = '';
            this.fetchedData.email = '';
            this.fetchedData.code = '';
            this.fetchedData.name = '';
        },
        clearParsedData(){
                this.parsedData.block = '';
                this.parsedData.id = '';
                this.parsedData.gender = '';
                this.parsedData.combo = false;
                this.parsedData.events = 0;
                this.parsedData.e1 = false;
                this.parsedData.e2 = false;
                this.parsedData.e3 = false;
                this.parsedData.e4 = false;
                this.parsedData.e5 =  false;  
        },
        clearEvents(){
            this.updateReq = false;
            this.combo = false;
            this.comboDisable = false;
            this.e1 = false;
            this.e2 = false;
            this.e3 = false;
            this.e4 = false;
            this.e5 = false;
        },
        fetchData(){
            this.id = this.id.toUpperCase();
            this.clearData();
            firebaseInit.collection('participants').where("student_id", "==",this.id).get()
                .then(snapshot =>{
                    snapshot.forEach(doc =>{
                        this.updateReq = true;
                        this.fetchedData = doc.data();
                        this.parseCode();
                    })
                })
                .catch(err =>{
                    alert("arr: "+err);
                })
        },
        otherPressed(){
            if( this.e1 == true ||this.e2 == true || this.e3 == true || this.e4 == true || this.e5 == true ){
                this.comboDisable = true;
            }else{
                this.comboDisable = false;
            }
        },
        calculateEvents(){
            this.events = 0;
            if(this.combo)
                return 32;  
            else{
                let sum =0;
                if(this.e1)
                    sum += 1;
                if(this.e2)
                    sum += 2;
                if(this.e3)
                    sum += 4;
                if(this.e4)
                    sum += 8;
                if(this.e5)
                    sum += 16;
                this.events = sum;
            }
            return this.events;
        },
        parseCode(){
            this.parsedData.block = this.fetchedData.code.substring(0,1);
            this.parsedData.id = this.fetchedData.code.substring(1,8);
            this.parsedData.gender = this.fetchedData.code.substring(8,9);
            this.gender = this.parsedData.gender;
            this.parsedData.events = this.fetchedData.code.substring(9,11);
            this.parsedData.events = parseInt(this.parsedData.events,10).toString(2);
            while(this.parsedData.events.length < 6){
                this.parsedData.events = '0'+this.parsedData.events;
            }
            this.combo = this.parsedData.events[0] == 1? true:false;
            if(this.combo){
                this.parsedData.e1 = true;
                this.parsedData.e2 = true;
                this.parsedData.e3 = true;
                this.parsedData.e4 = true;
                this.parsedData.e5 = true;
                this.e1 = true;
                this.e2 = true;
                this.e3 = true;
                this.e4 = true;
                this.e5 = true;
            }else{
                this.parsedData.e1 = this.parsedData.events[5] == 1? true:false;
                this.parsedData.e2 = this.parsedData.events[4] == 1? true:false;
                this.parsedData.e3 = this.parsedData.events[3] == 1? true:false;
                this.parsedData.e4 = this.parsedData.events[2] == 1? true:false;
                this.parsedData.e5 = this.parsedData.events[1] == 1? true:false;
                if(this.parsedData.e1)
                    this.e1 = this.parsedData.e1;
                if(this.parsedData.e2)
                    this.e2 = this.parsedData.e2;
                if(this.parsedData.e3)
                    this.e3 = this.parsedData.e3;
                if(this.parsedData.e4)
                    this.e4 = this.parsedData.e4;
                if(this.parsedData.e5)
                    this.e5 = this.parsedData.e5;
            }
        },
        submit(){
            let events = this.calculateEvents();
            if(events<10)
                events = '0'+events;
            this.code = this.block+this.id+this.gender+events;
            if(this.updateReq){
                firebaseInit.collection('participants').doc(this.id).update({
                    code: this.code
                })
                    .then(()=>{
                        this.fetchData();
                        alert("Code: "+this.code);
                    })
                    .catch((err)=>{
                        alert("something went wrong "+err);
                    });
            }else{
                let currDate = new Date().toJSON().slice(0,10).replace(/-/g,'/');
                firebaseInit.collection('participants').doc(this.id).set({
                    name: this.name,
                    email: this.email,
                    mobile: this.mobile,
                    student_id: this.id,
                    code: this.code,
                    currentDateWithFormat: currDate,
                })
                .then(()=>{
                    alert("Code: "+this.code);
                    this.fetchData();
                })
                .catch(()=>{
                    alert("failed");
                })
            }
            
            
        }
    }
}
</script>
