<template>
    <div class="w-full h-screen flex justify-center items-center bg-gray-500">
        <form class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4"  @submit.prevent="login">
            <div class="mb-4">
            <label class="block text-gray-700 text-sm font-bold mb-2" for="username">
                Username
            </label>
            <input v-model="email" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Username">
            </div>
            <div class="mb-6">
            <label class="block text-gray-700 text-sm font-bold mb-2" for="password">
                Password
            </label>
            <input v-model="password" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************">
            </div>
            <div class="flex items-center justify-between">
            <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="submit   ">
                Sign In
            </button>
            </div>
            </form>
    </div>
</template>
<script>
import * as firebase from 'firebase/app';
import 'firebase/auth';
export default {
    name: 'login',
    data(){
        return{
            email: '',
            password: '',
            error: '',
        }
    },
    methods:{
        async login(){
            try{
                await firebase.auth().signInWithEmailAndPassword(this.email,this.password);
                this.$router.replace({name: "add"});
            }catch(err){
                this.error = err;
                alert(err.message);
            }
        }
    }
}

</script>
